.frame {
	overflow: hidden; }

.frame p {
	display: inline-block;
	color: $gold;
	border-left: 1px solid $gold;
	border-top: 1px solid $gold;
	border-bottom: 1px solid $gold;
	@include font-lig-norm();
	font-size: 18px;
	padding: 15px;
	width: 420px; }

.frame br {
    display: none; }

.frame::after {
	display: inline-block;
	position: relative;
	content: '';
	border-left: 1px solid $gold;
	border-bottom: 1px solid $gold;
	transform: rotate(45deg);
	width: 37px;
	height: 38px;
	margin-right: 0px;
	top: 12px;
	left: -23px; }

