.buttom_form {
	a {
		display: block;
		width: 285px;
		height: 50px;
		border-radius: 25px;
		background-color: $gold;
		text-align: center;
		// padding-top: 10px
		@include font-reg-norm();
		font-size: 18px;
		text-decoration: none;
		margin-top: 50px;
		color: #000000;
		&:hover {
			text-decoration: none;
			background-color: #ffe146; }
		&:active {
			background-color: #e2c014; }
		&::before {
			display: inline-block;
			content: '+';
			vertical-align: middle;
			font-weight: 400;
			font-size: 21px;
			padding-top: 11px;
			padding-right: 23px;
			margin-left: -30px;
			margin-right: 45px;
			border-right: 1px solid #000000;
			height: 50px; } } }

